.ripple-button > .ripple {
  width: 20px;
  height: 20px;
  position: absolute;
  background: #bbc3ce71;
  display: block;
  content: '';
  border-radius: 9999px;
  opacity: 1;
  -webkit-animation: 0.8s ease 1 forwards ripple-effect;
          animation: 0.8s ease 1 forwards ripple-effect;
}

